.containerMinion {
  display: flex;
}

.minion {
  margin-left: 30px;
  width: 100px;
  height: 100px;
  animation: minion 20s infinite;
  position: absolute;
}

/* nota : el porcentage en blanco son en base a los segundos de la animación,y los porcentajes que estan dentro de left y demás son en base a la pantalla. */

@keyframes minion {
  0% {
    left: 0%;
    transform: rotateY(180deg);
  }
  20% {
    left: calc(40%);
    transform: rotateY(180deg);
  }
  30% {
    left: calc(40%);
    transform: rotateY(0deg);
  }

  37% {
    left: calc(60%);
    transform: rotateY(0deg);
    transform: rotatez(180deg);
  }
  45% {
    left: calc(80%);
    transform: rotateY(0deg);
    transform: rotatez(360deg);
  }

  49% {
    left: calc(100% - 150px);
    transform: rotateY(180deg);
  }

  51% {
    left: calc(100% - 150px);
    transform: rotateY(0deg);
  }
  60% {
    left: calc(100% - 150px);
    transform: rotateY(0deg);
  }
  98% {
    left: 0px;
    transform: rotateY(0deg);
  }
  100% {
    left: 0px;
    transform: rotateY(180deg);
  }
}
